import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(private httpClient: HttpClient) { }
  
  //base_url= "http://weather-forecast-shakshi.us-west-2.elasticbeanstalk.com";
  base_url = "";

  public getWeather(lat, lng)
  {
	  var url= "http://weather-forecast-shakshi.us-west-2.elasticbeanstalk.com/weather/?lat=34.0322&long=-118.2836";
	  var url= this.base_url + "/weather/?lat=" + lat + "&long=" + lng;
	  console.log(url);
	  return this.httpClient.get(url);
  }
  
  public getDetailWeather(lat, lng, time)
  {
    var url= this.base_url + "/weather-detail/?lat=" + lat + "&long=" + lng + "&time=" + time;
    console.log(url);
    return this.httpClient.get(url);
  }

  public getCurrentLocation()
  {
	  var url= "http://ip-api.com/json";
	  return this.httpClient.get(url);
  }
  
  public getSeal(state)
  {
	  var url = this.base_url + "/state-seal/?state=" + state;
	  console.log(url);
	  return this.httpClient.get(url);
  }
  public getLatitudeLongitude(street, city, state)
  {
	  var url= "http://weather-forecast-shakshi.us-west-2.elasticbeanstalk.com/location/?street=shrine&city=losangeles&state=CA";
	  
	  var url= this.base_url + "/location/?street=" + street + "&city=" + city + "&state=" + state;
	  console.log(url);
	  return this.httpClient.get(url);
  }
  
  public getSuggestions(term)
  {
	  var url= this.base_url + "/suggestions/?term=" + term;
	  return this.httpClient.get(url);
  }
}
